@media (min-width: 1024px) and (max-width: 1699px) {
  html {
    font-size: 18px; }
  .my-apps {
    max-width: 1344px; } }

@media (min-width: 1700px) and (max-width: 1919px) {
  html {
    font-size: 20px; }
  .my-apps {
